.form{
    &--auth{
        max-width: 28rem;
        margin: 0 auto;
    }
}

.form__group{
    margin-bottom: 1.5rem;
    position: relative;

    &--md{
        margin-bottom: 2.5rem;
    }
}

.form__control{
    padding: .5rem 0 0.75rem;
    overflow: hidden;
    display: block;
    width: 100%;
    font-family: inherit;
    font-size: 1.4rem;
    color: $white;
    background: transparent;
    border: none;
    border-bottom: 1px solid #3F3F3F;
    border-radius: 0;
    box-shadow: none;
    &::placeholder{
        color: rgba($white, .2);
    }
    &--textarea{
        resize: none;
        height: 30px;
    }
    &:hover + .form__line{
        width: 100%;
    }
    margin-bottom: 1.2rem;
}

.form__line{
    width: 0;
    height: 1px;
    background-color: $blue;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 1;
    transition: width .2s ease-in-out;
}

.form__footer{
    &--center{
        text-align: center;
    }
}

.form__footer-list{
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
    line-height: 1.2;
    li{
        margin: 0 0.5rem;
    }
}