.comments__item{
    margin-bottom: 3rem;
    margin-top: 5rem;
    .comments__item {
    padding-left: 3rem;
    margin-top: 3rem;
    @include breakpoint($mobile-bp){
        padding-left: 1.5rem;
    }
    }
}

.comments__header{
    display: flex;
    margin-bottom: 1.5rem;
}

.comments__avatar{
    width: 3rem;
    height: 3rem;
    border-radius: 50%; 
}

.comments__author{
    padding-left: 1.5rem;
    flex-grow: 1;
    font-size: 1.2rem;
    line-height: 1.4;
}

.comments__name{
    font-weight: 500;
    color: $white;
}

.comments__pubdate{
    color: #757575;
}

.comments__text{
    font-size: 1.2rem;
    color: $white;
    margin-bottom: 1rem;
    line-height: 1.3;
}

.comments__reply{
    color: $blue;
    font-family: inherit;
    font-size: 1.2rem;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    line-height: 1.1;
    &:hover{
        text-decoration: underline;
    }
}