.pagination{
    display: flex;
    justify-content: center;
    margin-top: 8rem;

    @include breakpoint($tablet-bp){
        margin-top: 5rem;
    }
}

.pagination__item{
    margin: 0 .5rem;
}

.pagination__link{
    width: 4rem;
    height: 4rem;
    display: block;
    background-color: #0D0D0D;
    border-radius: 3px;
    text-align: center;
    color: $white;
    font-size: 1.4rem;
    transition: background-color .2s linear;
    line-height: 4rem;
    &:hover{
        background-color: lighten(#0D0D0D, 3%);
    }
    &.active{
        background-color: $blue;
    }
}