//родитель
.cabinet{
    display: flex;
    @include breakpoint($mobile-bp){
        flex-direction: column;
    }
}

.cabinet__form{
    width: 340px;

    @include breakpoint($mobile-bp){
        order: 2;
    }
}

.cabinet__avatar{
    flex-grow: 1;
    padding-left: 11rem;
    display: block;
    clear: both;
    img{
        display: block;
        margin-bottom: 1.5rem;
    }

    @include breakpoint($mobile-bp){
        order: 1;
        padding-left: 0;
        margin-bottom: 2rem;
    }
}

.cabinet__file{
    font-size: 1.4rem;
    color: $blue;
    text-decoration: underline;
    cursor: pointer;
    &:hover{
        text-decoration: none;
    }
    input{
        display: none;
    }
}