.post{
    box-shadow: 0 15px 25px rgba($black, .35);
    background-color: $primary;
    margin-bottom: 3rem;
    padding: 2.5rem;
    border-radius: 5px;
    overflow: hidden;

    @include breakpoint($mobile-bp){
        padding: 1.5rem;
    }
}

.post__content{
    margin-bottom: 2rem;
}

.post__title{
    margin-bottom: 1.5rem;
    font-size: 1.8rem;
    color: $white;
    font-weight: 500;
    a{
        color: inherit;
        text-decoration: none;
        &:hover{
            text-decoration: underline;
        }
    }
    &--mb0{
        margin-bottom: 0;
    }
}

.post__subtitle{
    font-size: 1.6rem;
    color: $white;
    font-weight: 500;
    margin-bottom: 1.9rem;
    margin-top: 3rem;
}

.post__description{
    color: #d2d2d2;
    font-size: 1.4rem;
}

.post__text{
    color: $white;
    font-size: 1.4rem;

    border-bottom: 2px solid #1c1c1c;
    padding-bottom: 1rem;

    h2, h3, h4, h5, h6{
        margin-bottom: 2rem;
        font-weight: 700;
    }
    h2{
        font-size: 1.5rem;
    }
    h3{
        font-size: 1.25em;
    }
    h4{
        font-size: 1.15em;
    }
    h5, h6{
        font-size: 1em;
    }
    ul, ol{
        margin-bottom: 2rem;
    }
    ol{
        list-style: decimal;
        list-style-position: inside;
    }
    table{
        width: 100%;
        margin-bottom: 2rem;
    }
    img{
        max-width: 100%;
        height: auto;
    }
    a{
        color: $blue;
    }
    p{
        margin-bottom: 2rem;
    }

}

.post__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include breakpoint($mobile-bp){
        flex-direction: column;
        align-items: flex-start;
    }
}

.post__data{
    color: #828282;
    line-height: 1.2;
    display: flex;

    @include breakpoint($mobile-bp){
        margin-bottom: 1rem;
        font-size: 1.2rem;
    }
}

.post__header{
    margin: 0 0 2.5rem;
    &--preview{
        margin: -2.5rem -2.5rem 2.5rem;
    }
}

.post__preview{
    display: block;
    max-width: 100%;
    height: auto;
}

.post__data-item{
    &:not(:last-child):after{
        content: '';
        display: inline-block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #c4c4c4;
        margin: 0 1.5rem;
        position: relative;
        top: -2px;
    }
}

.post__read{
    color: #107EFF;
    font-size: 1.4rem;
}

.post__actions{
    margin-bottom: 1.5rem;
    display: flex;
    justify-content: space-between;
    font-size: 1.2rem;
    color: $white;
    a{
        opacity: .6;
        transition: opacity .2s linear;
        &:hover{
            opacity: 1;
        }
    }
}

.post__actions-icon{
    margin-left: .5rem;
}