.related{
    padding-bottom: .5rem;
    border-bottom: 2px solid #1c1c1c;
}

.related__list-item{
    margin-bottom: 2.5rem;
    width: 50%;

    @include breakpoint($mobile-bp){
        width: 100%;
    }
}

.related__list{
    display: flex;
    flex-wrap: wrap;

    @include breakpoint($mobile-bp){
        flex-direction: column;
    }
}

.related__list-title{
    font-size: 1.4rem;
    color: #F3EBA2;
    font-weight: 500;  
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap; 
}

.related__list-date{
    color: #828282;
    font-size: 1.2rem;
}