.modal{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: $z-index-modal;
    background-color: rgba($black, .8);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 1rem;
    display: none;
    &.show{
        display: flex;
    }
}

.modal__content{
    max-width: 500px;
    padding: 8rem 8rem;
    background-color: $primary;
    border-radius: 5px;
    width: 100%;
    position: relative;
    &--contact{
        padding-bottom: 2.5rem;
    }
    transform: translateY(-50px);
    // opacity: 0;
    transition: transform 200ms ease-in-out, opacity ease-in-out 200ms;
}
.modal__close{
    position: absolute;
    top: 2rem;
    right: 2rem;
    background: none;
    padding: 0;
    border: none;
    cursor: pointer;
    opacity: .5;
    transition: transform .2s linear, opacity .1s linear;
    &:hover{
        transform: rotate(180deg);
        opacity: 1;
    }
}

.modal__footer{
    display: flex;
    justify-content: center;
    color: #B2B2B2;
    font-size: 1.2rem;
    margin-top: 4rem;
    li{
        margin: 0 .5rem;
    }
    a{
        text-decoration: underline;
        &:hover{
            text-decoration: none;
        }
    }
}

.modal__video{
    display: block;
}

.modal__content--story{
    background: none;
}